<script>
export default {
  data() {
    const item = {
      date: "2016-05-02",
      name: "",
      address: " 1518 "
    };
    return {
      tableData: Array(20).fill(item)
    };
  },
  computed: {
    currentUser(){
      return localStorage.currentUser
    },
    userId() {
      return localStorage.userId
    }
  },
  methods: {
    handleCommand(command) {
      switch (command) {
        case "userEdit": 
          console.log(this.userId)
          this.$router.push(`/admin_users/edit/${this.userId}`);
          break
        case "exit":
          localStorage.clear()
          this.$router.push("/login")
          break
      }
    }
  }
};
